.gg-play-button {
	box-sizing: border-box;
	position: relative;
	display: block;
	transform: scale(var(--ggs,1));
	width: 22px;
	height: 22px;
	&::before {
		content: "";
		display: block;
		box-sizing: border-box;
		position: absolute;
		width: 0;
		height: 10px;
		border-top: 5px solid transparent;
		border-bottom: 5px solid transparent;
		border-left: 6px solid;
		top: 6px;
		left: 9px;
        color: #09A9DB;
	}
}
.offcanvas-header{
	@include max(767px){
		display: flex;
		justify-content: flex-end;
	}
}